    background-repeat: no-repeat;
    background-position: left 16px top 50%;
  }

  .dropdown-menu__title {
    display: block;
    font-size: 12px;
    line-height: 120%;
  }
}

.breadcrumbs-stock__download {
  position: relative;
  display: flex;
}

